<template>
  <div>
    
    <CRow class="mb-3">
      <CCol>
        <h1>Semua Thread</h1>
      </CCol>
      <CCol col="2" class="d-inline-flex justify-content-end align-items-center">
        
        <CButton block color="primary" @click="goAddThread()">Add Thread</CButton>
        
      </CCol>
    </CRow>
    
    <CRow>
      <CCol col="12" v-for="(thread,idx) in threads" :key="idx">
        <CCard>
          <CCardHeader>
            {{thread.thread_title}}

          </CCardHeader>
          <CCardBody>
            <div flex class="flex-row">
              <div inline-flex >
                <span v-html="thread.thread_content"></span>
              </div>
            </div>
          </CCardBody>
          <CCardFooter>
            <CRow>
              <CCol col="6">
                <CRow class="mx-0">
                  <Ccol class="px-0">
                    <div inline-block class="mr-2">
                      Replay {{thread.total_reply_forum}}
                    </div>
                  </Ccol>
                  <Ccol class="px-0">
                    <div inline-block class="mr-2">
                      View {{thread.total_view}}
                    </div>
                  </Ccol>
                </CRow>
              </CCol>
              <CCol col="6">
                <CRow class="justify-content-end">
                  <CCol col="6" sm="4" md="2" xl="2" class="mb-3 mb-xl-0">
                    <CButton block variant="outline" color="primary" @click="goEditThread(thread.id)"><CIcon name="cil-pencil"/> Edit</CButton>
                  </CCol>
                </CRow>
              </CCol>
            </CRow>
            
          </CCardFooter>
        </CCard>
      </CCol>
    </CRow>
    
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: 'IndexForum',
  data(){
    return {
      threads: []
    }
  },
  methods: {
    setThread(data){
      this.threads = data;
    },
    goAddThread(){
      this.$router.push('/thread/add');
    },
    goEditThread(id){
      this.$router.push({name: 'EditThread', params: { threadId: id }});
      localStorage.removeItem('threadId');
      localStorage.setItem('threadId', id);
    },
  },
  created() {
    let body = {
      admin_user_id : localStorage.getItem('admin_id')
    };

    axios
      .post('http://103.151.215.74/api/admin/forum/thread/list', body, {
        headers: {
          Authorization: localStorage.getItem('token')
        }
      })
      .then(
        // (response) => this.setThread(response.data.json_data)
        (response) => {
          // console.log("Berhasil : ", response);
          this.setThread(response.data.json_data);
        }
        
      )
      // .catch(function (error) {
      //   // handle error
      //   // console.log("Gagal : ", error);
      // })
  },
  mounted() {
    this.replaceString;
  }
}
</script>